.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.App-header {

  background-color: #222;

  height: 10px;

  padding: 20px;

  color: white;

}



.App-title {

  margin: 0;

}

.row{

    padding: 15px;

    background: #462727;

    color: white;

    margin-top: 4px;

    width: 90%;

    margin: 0 auto;

    margin-top: 5px;

    border-radius: 8px;

}

.row label{

  float:left;

  width:40%;

}



